export class Renderer {
  // ***** PRIVATE ******
  canvas: any;
  ctx: any;
  zoomFactor = 3;
  zoomFactorDelta = 0.4;
  gridSizeX = 200;
  gridSizeY = 200;
  gridStep = 10;
  ctxHeight: any;
  ctxWidth: any;
  prevX: number | undefined = 0;
  prevY: number | undefined = 0;

  //    var colorGrid="#bbbbbb", colorLine="#000000";
  sliderHor: any;
  sliderVer: any;
  layerNumStore: any;
  progressStore = { from: 0, to: -1 };
  lastX: any;
  lastY: any;
  dragStart: any;
  dragged: any;
  scaleFactor = 1.1;
  model: any;
  initialized = false;
  displayType = { speed: 1, expermm: 2, volpersec: 3 };
  renderOptions: any = {
    showMoves: true,
    showRetracts: true,
    colorGrid: "#bbbbbb",
    extrusionWidth: 1,
    //        colorLine: ["#000000", "#aabb88",  "#ffe7a0", "#6e7700", "#331a00", "#44ba97", "#08262f", "#db0e00", "#ff9977"],
    colorLine: [
      "#000000",
      "#45c7ba",
      "#a9533a",
      "#ff44cc",
      "#dd1177",
      "#eeee22",
      "#ffbb55",
      "#ff5511",
      "#777788",
      "#ff0000",
      "#ffff00",
    ],
    colorLineLen: 9,
    gradientColors: [
      [110, 64, 110],
      [0, 0, 255],
      [0, 255, 255],
      [0, 170, 0],
      [232, 232, 0],
      [170, 0, 0],
      [255, 0, 255],
    ],
    colorMove: "#00ff00",
    colorRetract: "#ff0000",
    colorRestart: "#0000ff",
    sizeRetractSpot: 2,
    modelCenter: { x: 0, y: 0 },
    moveModel: true,
    differentiateColors: true,
    showNextLayer: false,
    alpha: false,
    actualWidth: false,
    renderErrors: false,
    renderAnalysis: false,
    speedDisplayType: this.displayType.speed,
  };

  offsetModelX = 0;
  offsetModelY = 0;
  speeds: any = [];
  speedsByLayer: any = {};
  volSpeeds: any = [];
  volSpeedsByLayer: any = {};
  extrusionSpeeds: any = [];
  extrusionSpeedsByLayer: any = {};
  max: any = {};
  min: any = {};

  private rgbToColor(r: any, g: any, b: any) {
    let rString = Math.round(r).toString(16);
    let gString = Math.round(g).toString(16);
    let bString = Math.round(b).toString(16);
    if (rString.length < 2) {
      rString = "0" + rString;
    }
    if (gString.length < 2) {
      gString = "0" + gString;
    }
    if (bString.length < 2) {
      bString = "0" + bString;
    }
    return "#" + rString + gString + bString;
  }

  private gradientColor(scale: any) {
    // There's still a rounding error somewhere, hence the .1.
    // I leave it to some diligent programmer to find and fix this.
    if (scale < -0.1 || scale > 1.1) {
      return "#000000";
    }
    if (scale > 1) {
      scale = 1;
    } else if (scale < 0) {
      scale = 0;
    }
    const numSegments = this.renderOptions["gradientColors"].length;
    const leftIndex = Math.floor(scale * (numSegments - 1));
    const mix = scale * (numSegments - 1) - leftIndex;
    const leftColor = this.renderOptions["gradientColors"][leftIndex];
    const rightColor =
      this.renderOptions["gradientColors"][
        Math.ceil(scale * (numSegments - 1))
      ];
    return this.rgbToColor(
      leftColor[0] * (1.0 - mix) + rightColor[0] * mix,
      leftColor[1] * (1.0 - mix) + rightColor[1] * mix,
      leftColor[2] * (1.0 - mix) + rightColor[2] * mix
    );
  }

  private reRender() {
    const gCodeOpts = this.getOptions();
    const p1 = this.ctx.transformedPoint(0, 0);
    const p2 = this.ctx.transformedPoint(this.canvas.width, this.canvas.height);
    this.ctx.clearRect(p1.x, p1.y, p2.x - p1.x, p2.y - p1.y);
    this.drawGrid();
    if (this.renderOptions["alpha"]) {
      this.ctx.globalAlpha = 0.6;
    } else {
      this.ctx.globalAlpha = 1;
    }
    if (this.renderOptions["actualWidth"]) {
      this.renderOptions["extrusionWidth"] =
        (gCodeOpts["filamentDia"] * gCodeOpts["wh"]) / this.zoomFactor;
    } else {
      this.renderOptions["extrusionWidth"] =
        (gCodeOpts["filamentDia"] * gCodeOpts["wh"]) / this.zoomFactor / 2;
    }
    if (
      this.renderOptions["showNextLayer"] &&
      this.layerNumStore < this.model.length - 1
    ) {
      this.drawLayer(
        this.layerNumStore + 1,
        0,
        this.getLayerNumSegments(this.layerNumStore + 1),
        true
      );
    }
    this.drawLayer(
      this.layerNumStore,
      this.progressStore.from,
      this.progressStore.to,
      undefined
    );
  }

  private trackTransforms(ctx: any) {
    const svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
    let xform = svg.createSVGMatrix();
    ctx.getTransform = function () {
      return xform;
    };

    const savedTransforms: any = [];
    const save = ctx.save;
    ctx.save = function () {
      savedTransforms.push(xform.translate(0, 0));
      return save.call(ctx);
    };
    const restore = ctx.restore;
    ctx.restore = function () {
      xform = savedTransforms.pop();
      return restore.call(ctx);
    };

    const scale = ctx.scale;
    ctx.scale = function (sx: any, sy: any) {
      xform = xform.scaleNonUniform(sx, sy);
      return scale.call(ctx, sx, sy);
    };
    const rotate = ctx.rotate;
    ctx.rotate = function (radians: any) {
      xform = xform.rotate((radians * 180) / Math.PI);
      return rotate.call(ctx, radians);
    };
    const translate = ctx.translate;
    ctx.translate = function (dx: any, dy: any) {
      xform = xform.translate(dx, dy);
      return translate.call(ctx, dx, dy);
    };
    const transform = ctx.transform;
    ctx.transform = function (a: any, b: any, c: any, d: any, e: any, f: any) {
      const m2 = svg.createSVGMatrix();
      m2.a = a;
      m2.b = b;
      m2.c = c;
      m2.d = d;
      m2.e = e;
      m2.f = f;
      xform = xform.multiply(m2);
      return transform.call(ctx, a, b, c, d, e, f);
    };
    const setTransform = ctx.setTransform;
    ctx.setTransform = function (
      a: any,
      b: any,
      c: any,
      d: any,
      e: any,
      f: any
    ) {
      xform.a = a;
      xform.b = b;
      xform.c = c;
      xform.d = d;
      xform.e = e;
      xform.f = f;
      return setTransform.call(ctx, a, b, c, d, e, f);
    };
    const pt = svg.createSVGPoint();
    ctx.transformedPoint = function (x: any, y: any) {
      pt.x = x;
      pt.y = y;
      return pt.matrixTransform(xform.inverse());
    };
  }

  private startCanvas() {
    this.canvas = document.getElementById("canvas");

    // Проверяем понимает ли браузер canvas
    if (!this.canvas.getContext) {
      throw "exception";
    }

    this.ctx = this.canvas.getContext("2d"); // Получаем 2D контекст
    this.ctxHeight = this.canvas.height;
    this.ctxWidth = this.canvas.width;
    this.lastX = this.ctxWidth / 2;
    this.lastY = this.ctxHeight / 2;
    this.ctx.lineWidth = 2;
    this.ctx.lineCap = "round";
    this.trackTransforms(this.ctx);

    this.canvas.addEventListener(
      "mousedown",
      (evt: any) => {
        document.body.style.webkitUserSelect = document.body.style.userSelect =
          "none";
        this.lastX = evt.offsetX || evt.pageX - this.canvas.offsetLeft;
        this.lastY = evt.offsetY || evt.pageY - this.canvas.offsetTop;
        this.dragStart = this.ctx.transformedPoint(this.lastX, this.lastY);
        this.dragged = false;
      },
      false
    );
    this.canvas.addEventListener(
      "mousemove",
      (evt: any) => {
        this.lastX = evt.offsetX || evt.pageX - this.canvas.offsetLeft;
        this.lastY = evt.offsetY || evt.pageY - this.canvas.offsetTop;
        this.dragged = true;
        if (this.dragStart) {
          const pt = this.ctx.transformedPoint(this.lastX, this.lastY);
          this.ctx.translate(pt.x - this.dragStart.x, pt.y - this.dragStart.y);
          this.reRender();
        }
      },
      false
    );
    this.canvas.addEventListener(
      "mouseup",
      (evt: any) => {
        this.dragStart = null;
        if (!this.dragged) zoom(evt.shiftKey ? -1 : 1);
      },
      false
    );

    let zoom = (clicks: any) => {
      const pt = this.ctx.transformedPoint(this.lastX, this.lastY);
      this.ctx.translate(pt.x, pt.y);
      const factor = Math.pow(this.scaleFactor, clicks);
      this.ctx.scale(factor, factor);
      this.ctx.translate(-pt.x, -pt.y);
      this.reRender();
    };
    const handleScroll = (evt: any) => {
      let delta;
      if (evt.detail < 0 || evt.wheelDelta > 0) delta = this.zoomFactorDelta;
      else delta = -1 * this.zoomFactorDelta;
      if (delta) zoom(delta);
      return evt.preventDefault() && false;
    };
    this.canvas.addEventListener("DOMMouseScroll", handleScroll, false);
    this.canvas.addEventListener("mousewheel", handleScroll, false);
  }

  private drawGrid() {
    let i;
    this.ctx.strokeStyle = this.renderOptions["colorGrid"];
    this.ctx.lineWidth = 1;
    let offsetX = 0,
      offsetY = 0;
    if (this.renderOptions["moveModel"]) {
      offsetX = this.offsetModelX;
      offsetY = this.offsetModelY;
    }

    this.ctx.beginPath();
    for (i = 0; i <= this.gridSizeX; i += this.gridStep) {
      this.ctx.moveTo(i * this.zoomFactor - offsetX, 0 - offsetY);
      this.ctx.lineTo(
        i * this.zoomFactor - offsetX,
        -this.gridSizeY * this.zoomFactor - offsetY
      );
    }
    this.ctx.stroke();

    this.ctx.beginPath();
    for (i = 0; i <= this.gridSizeY; i += this.gridStep) {
      this.ctx.moveTo(0 - offsetX, -i * this.zoomFactor - offsetY);
      this.ctx.lineTo(
        this.gridSizeX * this.zoomFactor - offsetX,
        -i * this.zoomFactor - offsetY
      );
    }
    this.ctx.stroke();
  }

  private drawLayer(
    layerNum: any,
    fromProgress: any,
    toProgress: any,
    isNextLayer: any
  ) {
    let i,
      speedIndex = 0,
      //prevZ = 0,
      speedScale = 0;
    isNextLayer = typeof isNextLayer !== "undefined" ? isNextLayer : false;
    if (!isNextLayer) {
      this.layerNumStore = layerNum;
      this.progressStore = { from: fromProgress, to: toProgress };
    }
    if (!this.model || !this.model[layerNum]) return;

    const cmds = this.model[layerNum];
    let x, y;

    //        if(toProgress === -1){
    //            toProgress=cmds.length;
    //        }

    if (fromProgress > 0) {
      this.prevX = cmds[fromProgress - 1].x * this.zoomFactor;
      this.prevY = -cmds[fromProgress - 1].y * this.zoomFactor;
    } else if (fromProgress === 0 && layerNum == 0) {
      if (
        this.model[0] &&
        typeof this.model[0].x !== "undefined" &&
        typeof this.model[0].y !== "undefined"
      ) {
        this.prevX = this.model[0].x * this.zoomFactor;
        this.prevY = -this.model[0].y * this.zoomFactor;
      } else {
        this.prevX = 0;
        this.prevY = 0;
      }
    } else if (
      typeof cmds[0].prevX !== "undefined" &&
      typeof cmds[0].prevY !== "undefined"
    ) {
      this.prevX = cmds[0].prevX * this.zoomFactor;
      this.prevY = -cmds[0].prevY * this.zoomFactor;
    } else {
      if (this.model[layerNum - 1]) {
        this.prevX = undefined;
        this.prevY = undefined;
        for (i = this.model[layerNum - 1].length - 1; i >= 0; i--) {
          if (
            typeof this.prevX === "undefined" &&
            this.model[layerNum - 1][i].x !== undefined
          )
            this.prevX = this.model[layerNum - 1][i].x * this.zoomFactor;
          if (
            typeof this.prevY === "undefined" &&
            this.model[layerNum - 1][i].y !== undefined
          )
            this.prevY = -this.model[layerNum - 1][i].y * this.zoomFactor;
        }
        if (typeof this.prevX === "undefined") this.prevX = 0;
        if (typeof this.prevY === "undefined") this.prevY = 0;
      } else {
        this.prevX = 0;
        this.prevY = 0;
      }
    }

    //prevZ = this.getZ(layerNum);

    //        ctx.strokeStyle = renderOptions["colorLine"];
    for (i = fromProgress; i <= toProgress; i++) {
      this.ctx.lineWidth = 1;

      if (typeof cmds[i] === "undefined") continue;

      if (
        typeof cmds[i].prevX !== "undefined" &&
        typeof cmds[i].prevY !== "undefined"
      ) {
        this.prevX = cmds[i].prevX * this.zoomFactor;
        this.prevY = -cmds[i].prevY * this.zoomFactor;
      }
      //                console.log(cmds[i]);
      if (typeof cmds[i].x === "undefined" || isNaN(cmds[i].x))
        x = this.prevX / this.zoomFactor;
      else x = cmds[i].x;
      if (typeof cmds[i].y === "undefined" || isNaN(cmds[i].y))
        y = this.prevY / this.zoomFactor;
      else y = -cmds[i].y;
      if (
        this.renderOptions["differentiateColors"] &&
        !this.renderOptions["showNextLayer"] &&
        !this.renderOptions["renderAnalysis"]
      ) {
        //                if(speedsByLayer['extrude'][prevZ]){
        if (this.renderOptions["speedDisplayType"] === this.displayType.speed) {
          speedIndex = this.speeds["extrude"].indexOf(cmds[i].speed);
          speedScale =
            (cmds[i].speed - this.min.speed) /
            (this.max.speed - this.min.speed);
        } else if (
          this.renderOptions["speedDisplayType"] === this.displayType.expermm
        ) {
          speedIndex = this.volSpeeds.indexOf(cmds[i].volPerMM);
          speedScale =
            (cmds[i].volPerMM - this.min.volSpeed) /
            (this.max.volSpeed - this.min.volSpeed);
        } else if (
          this.renderOptions["speedDisplayType"] === this.displayType.volpersec
        ) {
          const volpersec = ((cmds[i].volPerMM * cmds[i].speed) / 60).toFixed(
            3
          );
          speedIndex = this.extrusionSpeeds.indexOf(volpersec);
          speedScale =
            (parseFloat(volpersec) - this.min.extrSpeed) /
            (this.max.extrSpeed - this.min.extrSpeed);
        } else {
          speedIndex = 0;
          speedScale = -1;
        }
        //                    speedIndex = GCODE.ui.ArrayIndexOf(speedsByLayer['extrude'][prevZ], function(obj) {return obj.speed === cmds[i].speed;});
        //                } else {
        //                    speedIndex = -1;
        //                }
        if (speedIndex === -1) {
          speedIndex = 0;
        } else if (speedIndex > this.renderOptions["colorLineLen"] - 1) {
          speedIndex = speedIndex % (this.renderOptions["colorLineLen"] - 1);
          //                console.log("Too much colors");
        }
      } else if (this.renderOptions["showNextLayer"] && isNextLayer) {
        speedIndex = 3;
      } else if (this.renderOptions["renderErrors"]) {
        if (cmds[i].errType === 2) {
          speedIndex = 9;
          //                    console.log("l: " + layerNum + " c: " + i);
        } else if (cmds[i].errType === 1) {
          speedIndex = 10;
        } else {
          speedIndex = 0;
        }
      } else if (this.renderOptions["renderAnalysis"]) {
        //                if(cmds[i].errType === 2){
        //                    speedIndex=-1;
        //                }else{
        //                    speedIndex=0;
        //                }
        if (layerNum !== 0) speedIndex = -1;
        else speedIndex = 0;
      } else {
        speedIndex = 0;
      }

      if (!cmds[i].extrude && !cmds[i].noMove) {
        //                ctx.stroke();
        if (cmds[i].retract == -1) {
          if (this.renderOptions["showRetracts"]) {
            this.ctx.strokeStyle = this.renderOptions["colorRetract"];
            this.ctx.fillStyle = this.renderOptions["colorRetract"];
            this.ctx.beginPath();
            this.ctx.arc(
              this.prevX,
              this.prevY,
              this.renderOptions["sizeRetractSpot"],
              0,
              Math.PI * 2,
              true
            );
            this.ctx.stroke();
            this.ctx.fill();
          }
        }
        if (this.renderOptions["showMoves"]) {
          this.ctx.strokeStyle = this.renderOptions["colorMove"];
          this.ctx.beginPath();
          this.ctx.moveTo(this.prevX, this.prevY);
          this.ctx.lineTo(x * this.zoomFactor, y * this.zoomFactor);
          this.ctx.stroke();
        }
        //                ctx.strokeStyle = renderOptions["colorLine"][0];
        //                ctx.beginPath();
        //                console.log("moveto: "+cmds[i].x+":"+cmds[i].y)
        //                ctx.moveTo(cmds[i].x*zoomFactor,cmds[i].y*zoomFactor);
      } else if (cmds[i].extrude) {
        if (cmds[i].retract == 0) {
          if (speedScale >= 0) {
            this.ctx.strokeStyle = this.gradientColor(speedScale);
          } else if (speedIndex >= 0) {
            this.ctx.strokeStyle = this.renderOptions["colorLine"][speedIndex];
          } else if (speedIndex === -1) {
            let val = parseInt(cmds[i].errLevelB).toString(16);
            //                        var val = '8A';
            const crB = "#" + "00".substr(0, 2 - val.length) + val + "0000";
            val = parseInt(cmds[i].errLevelE).toString(16);
            const crE = "#" + "00".substr(0, 2 - val.length) + val + "0000";
            //                        if(renderOptions['showMoves'])console.log(cr);
            const gradient = this.ctx.createLinearGradient(
              this.prevX,
              this.prevY,
              x * this.zoomFactor,
              y * this.zoomFactor
            );
            if (cmds[i].errType === 1) {
              let limit = 1 - cmds[i].errDelimiter;
              if (limit >= 0.99) limit = 0.99;
              gradient.addColorStop(0, "#000000");
              gradient.addColorStop(limit, "#000000");
              gradient.addColorStop(limit + 0.01, crE);
              gradient.addColorStop(1, crE);
            } else if (cmds[i].errType === 2) {
              gradient.addColorStop(0, crB);
              let limit = cmds[i].errDelimiter;
              if (limit >= 0.99) limit = 0.99;
              gradient.addColorStop(limit, crB);
              gradient.addColorStop(limit + 0.01, "#000000");
              gradient.addColorStop(1, "#000000");
            } else {
              gradient.addColorStop(0, crB);
              gradient.addColorStop(1, crE);
            }
            this.ctx.strokeStyle = gradient;
          }
          this.ctx.lineWidth = this.renderOptions["extrusionWidth"];
          this.ctx.beginPath();
          this.ctx.moveTo(this.prevX, this.prevY);
          this.ctx.lineTo(x * this.zoomFactor, y * this.zoomFactor);
          this.ctx.stroke();
        } else {
          if (this.renderOptions["showRetracts"]) {
            //                        ctx.stroke();
            this.ctx.strokeStyle = this.renderOptions["colorRestart"];
            this.ctx.fillStyle = this.renderOptions["colorRestart"];
            this.ctx.beginPath();
            this.ctx.arc(
              this.prevX,
              this.prevY,
              this.renderOptions["sizeRetractSpot"],
              0,
              Math.PI * 2,
              true
            );
            this.ctx.stroke();
            this.ctx.fill();
            //                        ctx.strokeStyle = renderOptions["colorLine"][0];
            //                        ctx.beginPath();
          }
        }
      }
      this.prevX = x * this.zoomFactor;
      this.prevY = y * this.zoomFactor;
    }
    this.ctx.stroke();
  }

  // ***** PUBLIC *******
  public init() {
    this.startCanvas();
    this.initialized = true;
    this.ctx.translate(
      (this.canvas.width - this.gridSizeX * this.zoomFactor) / 2,
      this.gridSizeY * this.zoomFactor +
        (this.canvas.height - this.gridSizeY * this.zoomFactor) / 2
    );
  }
  public setOption(options: any) {
    for (const opt in options) {
      // eslint-disable-next-line no-prototype-builtins
      if (options.hasOwnProperty(opt)) {
        this.renderOptions[opt] = options[opt];
        //                    console.log("Got a set option call: " + opt + " == " + options[opt]);
      }
    }

    if (this.initialized) this.reRender();
  }
  public getOptions() {
    return this.renderOptions;
  }
  public debugGetModel() {
    return this.model;
  }
  public render(layerNum: any, fromProgress: any, toProgress: any) {
    const gCodeOpts = this.getOptions();
    if (!this.initialized) this.init();
    if (!this.model) {
      this.drawGrid();
    } else {
      if (layerNum < this.model.length) {
        const p1 = this.ctx.transformedPoint(0, 0);
        const p2 = this.ctx.transformedPoint(
          this.canvas.width,
          this.canvas.height
        );
        this.ctx.clearRect(p1.x, p1.y, p2.x - p1.x, p2.y - p1.y);
        this.drawGrid();
        if (this.renderOptions["alpha"]) {
          this.ctx.globalAlpha = 0.6;
        } else {
          this.ctx.globalAlpha = 1;
        }
        if (this.renderOptions["actualWidth"]) {
          this.renderOptions["extrusionWidth"] =
            (gCodeOpts["filamentDia"] * gCodeOpts["wh"]) / this.zoomFactor;
        } else {
          this.renderOptions["extrusionWidth"] =
            (gCodeOpts["filamentDia"] * gCodeOpts["wh"]) / this.zoomFactor / 2;
        }
        if (
          this.renderOptions["showNextLayer"] &&
          layerNum < this.model.length - 1
        ) {
          this.drawLayer(
            layerNum + 1,
            0,
            this.getLayerNumSegments(layerNum + 1),
            true
          );
        }
        this.drawLayer(layerNum, fromProgress, toProgress, undefined);
      } else {
        console.log("Got request to render non-existent layer!!");
      }
    }
  }
  public getModelNumLayers() {
    return this.model ? this.model.length : 1;
  }
  public getLayerNumSegments(layer: any) {
    if (this.model) {
      return this.model[layer] ? this.model[layer].length : 1;
    } else {
      return 1;
    }
  }
  public doRender(mdl: any, layerNum: any) {
    let mdlInfo;
    this.model = mdl;
    this.prevX = 0;
    this.prevY = 0;
    if (!this.initialized) this.init();

    mdlInfo = mdl.getModelInfo(); // Need to read from gCodeReader
    this.speeds = mdlInfo.speeds;
    this.speedsByLayer = mdlInfo.speedsByLayer;
    this.volSpeeds = mdlInfo.volSpeeds;
    this.volSpeedsByLayer = mdlInfo.volSpeedsByLayer;
    this.extrusionSpeeds = mdlInfo.extrusionSpeeds;
    this.extrusionSpeedsByLayer = mdlInfo.extrusionSpeedsByLayer;
    this.max = mdlInfo.max;
    this.min = mdlInfo.min;
    //            console.log(speeds);
    //            console.log(mdlInfo.min.x + ' ' + mdlInfo.modelSize.x);
    this.offsetModelX =
      (this.gridSizeX / 2 - (mdlInfo.min.x + mdlInfo.modelSize.x / 2)) *
      this.zoomFactor;
    this.offsetModelY =
      (mdlInfo.min.y + mdlInfo.modelSize.y / 2) * this.zoomFactor -
      (this.gridSizeY / 2) * this.zoomFactor;
    if (this.ctx) this.ctx.translate(this.offsetModelX, this.offsetModelY);
    const scaleF =
      mdlInfo.modelSize.x > mdlInfo.modelSize.y
        ? this.canvas.width / mdlInfo.modelSize.x / this.zoomFactor
        : this.canvas.height / mdlInfo.modelSize.y / this.zoomFactor;
    const pt = this.ctx.transformedPoint(
      this.canvas.width / 2,
      this.canvas.height / 2
    );
    const transform = this.ctx.getTransform();
    const sX = scaleF / transform.a,
      sY = scaleF / transform.d;
    this.ctx.translate(pt.x, pt.y);
    this.ctx.scale(0.98 * sX, 0.98 * sY);
    this.ctx.translate(-pt.x, -pt.y);
    //            ctx.scale(scaleF,scaleF);
    this.render(layerNum, 0, this.model[layerNum].length);
  }
  public getZ(layerNum: any) {
    if (!this.model && !this.model[layerNum]) {
      return "-1";
    }
    const cmds = this.model[layerNum];
    for (let i = 0; i < cmds.length; i++) {
      if (cmds[i].prevZ !== undefined) return cmds[i].prevZ;
    }
    return "-1";
  }
  public getGradientColor(scale: any) {
    return this.gradientColor(scale);
  }
}
